import { Grid, Paper, Typography } from '@mui/material';
import React from 'react'
import { useParams } from 'react-router-dom'
import useFetchElection from '../hooks/useFetchElection';
const PaperStyle={
  padding:"30px 20px",width:1000,margin:"20px auto"
}
function Election() {
    const {electionid}=useParams()

  const { data: ele,electionStatus, error } = useFetchElection(electionid,"administrator");
    
  if (ele === null) {
    return <h1>Loading</h1>;
  }
  if (error) {
    console.log(error);
  }

  if (ele === undefined) {
    return <h1>Loading.....</h1>;
  }
  const local = (time) => {
    var date = new Date(time);
    return date.toLocaleString();
  };
  
  return (<>
    <Grid>
      
        <Paper elevation={20} style={PaperStyle}>
          <Grid>
          <Typography variant="h2" align="center">
              {ele.details.electionTitle}
            </Typography>

            <Typography variant="h6" align="center">
              {local(ele.details.electionStartAt)} To{" "}
              {local(ele.details.electionEndAt)}
            </Typography>

            <br />
            <Typography variant="h5">
              <b>Election Instruction :</b>

              {ele.ballots.ballotInstruction}
            </Typography>
            <br />
            <Typography variant="h5">
              <b>Position :</b>
              {ele.ballots.postionName}
            </Typography>
            <br />
            {electionStatus === "notStarted" ? (
              <>election will start at {ele.details.electionStartAt} </>
            ) : electionStatus === "Live" ?<>Live</> : (
              electionStatus === "Completed" && (
                <>Election completed Click here for results</>
              )
            )}
          </Grid>
        </Paper>
      
    </Grid>
    </>
  )
}

export default Election